::-webkit-scrollbar {
  width: 0.5rem;
}
::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.main_grid {
  display: flex;
  height: 100vh;
  /* grid-template-columns: fit-content(200px) 1fr; */
}

.sidebar-offset-height {
  overflow: hidden auto;
  height: calc(100vh);
}

.sidebar-ellipsis {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.tab-close-btn {
  border-radius: 6px;
  margin-left: 2px;
}
.tab-close-btn:hover {
  background-color: #ffffff33;
}

/* This is for react observable to stop showing me undelivered error screen */
/* Source: https://stackoverflow.com/questions/76187282/react-resizeobserver-loop-completed-with-undelivered-notifications/77094068#77094068 */
iframe#webpack-dev-server-client-overlay {
  display: none !important;
}
